import { Module, GetterTree, MutationTree, ActionTree } from "vuex";
import { IRootState } from ".";

interface IRepository {
  name: string;
  password: string;
  location: string;
  s3AccessKey: string;
  s3SecretKey: string;
  id: number;
}

interface IRepositoriesState {
  repositories: IRepository[];
}

const state: IRepositoriesState = {
  repositories: [
    {
      name: "Demo repo 1",
      password: "asdf",
      location: "s3.amazonaws.com/backup380-bucket-1",
      s3AccessKey: "S3EX4MPL3KEYASDFGHJK",
      s3SecretKey: "aS3exaMPLeSeCRE7ACCessK3y+qWertYuI/pzyxC",
      id: Math.floor(Math.random() * 1000000)
    },
    {
      name: "Demo repo 2",
      password: "asdf",
      location: "s3.amazonaws.com/backup380-bucket-2",
      s3AccessKey: "S3EX4MPL3KEYASDFGHJK",
      s3SecretKey: "aS3exaMPLeSeCRE7ACCessK3y+qWertYuI/pzyxC",
      id: Math.floor(Math.random() * 1000000)
    }
  ]
};

const getters: GetterTree<IRepositoriesState, IRootState> = {
  recentlyUsed(state) {
    // return at most 5 repositories (shown in side menu)
    return state.repositories.slice(0, 5);
  },
  allRepositories(state) {
    return state.repositories;
  }
};

export const repositoriesModule: Module<IRepositoriesState, IRootState> = {
  namespaced: true,
  state,
  mutations: {},
  getters
};
