import axios from "axios";
import { axiosConfig, transformResponse } from ".";

interface ILoginResponse {
  username: string;
  token: string;
}

interface ILoginArgs {
  username: string;
  password: string;
}

interface ILogoutResponse {}

interface ICreateUserArgs {
  username: string;
  password: string;
}

interface ICreateUserResponse {
  success: boolean;
}

interface IAnyUserExistsResponse {
  exists: boolean;
}

export const login = async ({
  username,
  password
}: Required<ILoginArgs>): Promise<ILoginResponse> => {
  const response = await axios.post(
    "/users/login",
    { username, password },
    { ...axiosConfig }
  );
  return transformResponse(response);
};

export const logout = async (): Promise<ILogoutResponse> => {
  const response = await axios.post("/users/logout", {}, { ...axiosConfig });
  return transformResponse(response);
};

export const createUser = async ({
  username,
  password
}: Required<ICreateUserArgs>): Promise<ICreateUserResponse> => {
  const response = await axios.post(
    "/user",
    { username, password },
    { ...axiosConfig }
  );
  return transformResponse(response);
};

export const anyUserExists = async (): Promise<IAnyUserExistsResponse> => {
  const response = await axios.get<IAnyUserExistsResponse>("/user/anyExists", {
    ...axiosConfig
  });
  return transformResponse(response);
};
