<template>
  <div>
    <v-navigation-drawer
      :clipped="$vuetify.breakpoint.lgAndUp"
      v-model="drawer"
      fixed
      app
      v-if="layout == 'default'"
    >
      <v-list>
        <template v-for="item in items">
          <v-subheader :key="item.heading" v-if="item.heading">{{ item.heading }}</v-subheader>
          <v-list-group
            v-else-if="item.children"
            v-model="item.model"
            :key="item.text"
            :prepend-icon="item.model ? item.icon : item['icon-alt']"
            append-icon
          >
            <v-list-tile slot="activator">
              <v-list-tile-content>
                <v-list-tile-title>{{ item.text }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile
              v-for="(child, i) in item.children"
              :key="i"
              :to="child.to"
              :exact="child.exact"
            >
              <v-list-tile-action>
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>{{ child.text }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list-group>
          <v-list-tile v-else :key="item.text" :to="item.to">
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>{{ item.text }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar :clipped-left="$vuetify.breakpoint.lgAndUp" color="primary" dark app fixed>
      <v-toolbar-side-icon v-if="layout=='default'" @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title style="width: 300px" class>
        <span class="hidden-sm-and-down">Backup380</span>
      </v-toolbar-title>
      <v-text-field
        v-if="layout=='default'"
        flat
        solo-inverted
        hide-details
        prepend-inner-icon="mdi-magnify"
        :label="$t('navigation.search')"
        class="hidden-sm-and-down"
      ></v-text-field>
      <v-spacer></v-spacer>
      <v-tooltip bottom>
        <v-btn slot="activator" icon v-if="layout=='default'" to="/events">
          <v-icon>mdi-bell</v-icon>
        </v-btn>
        <span>{{$t("navigation.notifications")}}</span>
      </v-tooltip>
      <v-tooltip bottom>
        <v-btn slot="activator" icon v-if="layout=='default'" @click="performLogout">
          <v-icon>mdi-logout-variant</v-icon>
        </v-btn>
        <span>{{$t("navigation.logout")}}</span>
      </v-tooltip>
    </v-toolbar>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  data: () => ({
    dialog: false,
    drawer: null
  }),
  computed: {
    items() {
      return [
        { heading: this.$t("navigation.general") },
        { icon: "mdi-home", text: this.$t("navigation.home"), to: "/" },
        {
          icon: "mdi-history",
          text: this.$t("navigation.events"),
          to: "/events"
        },
        {
          icon: "mdi-chevron-up",
          "icon-alt": "mdi-chevron-down",
          text: this.$t("navigation.repositories"),
          model: false,
          children: [
            {
              icon: "mdi-nas",
              text: this.$t("navigation.all-repositories"),
              to: "/repositories",
              exact: true
            },
            {
              icon: "mdi-plus",
              text: this.$t("navigation.create-repository"),
              to: "/repositories/new"
            },
            ...this.recentlyUsedRepositories.map(repo => ({
              text: repo.name,
              to: `/repositories/${repo.id}`
            }))
          ]
        },
        {
          icon: "mdi-chevron-up",
          "icon-alt": "mdi-chevron-down",
          text: this.$t("navigation.jobs"),
          model: false,
          children: [
            {
              icon: "mdi-calendar-check",
              text: this.$t("navigation.all-jobs"),
              to: "/jobs",
              exact: true
            },
            {
              icon: "mdi-plus",
              text: this.$t("navigation.create-job"),
              to: "/jobs/new"
            },
            ...this.recentlyUsedJobs.map(job => ({
              text: job.name,
              to: `/jobs/${job.id}`
            }))
          ]
        },
        { heading: this.$t("navigation.other") },
        {
          icon: "mdi-settings",
          text: this.$t("navigation.settings"),
          to: "/settings"
        },
        { icon: "mdi-help", text: this.$t("navigation.help"), to: "/help" }
      ];
    },
    layout() {
      return this.$route.meta.layout || "default";
    },
    ...mapGetters({
      recentlyUsedRepositories: "repositories/recentlyUsed",
      recentlyUsedJobs: "jobs/recentlyUsed"
    })
  },
  methods: {
    ...mapActions({
      logout: "auth/logout"
    }),
    async performLogout() {
      await this.logout();
      this.$router.push("/login");
    }
  }
};
</script>
