import { Module, GetterTree } from "vuex";
import { IRootState } from ".";

export interface IJob {
  name: string;
  id: number;
}

export interface IJobsState {
  jobs: IJob[];
}

const getters: GetterTree<IJobsState, IRootState> = {
  recentlyUsed(state) {
    // return at most 5 jobs (shown in side menu)
    return state.jobs.slice(0, 5);
  }
};

export const jobsModule: Module<IJobsState, IRootState> = {
  namespaced: true,
  state: {
    jobs: [
      { name: "My daily job", id: Math.floor(Math.random() * 1000000) },
      { name: "Weekly backup", id: Math.floor(Math.random() * 1000000) },
      { name: "Important files", id: Math.floor(Math.random() * 1000000) }
    ]
  },
  mutations: {},
  getters
};
