import { Module, GetterTree, MutationTree, ActionTree } from "vuex";
import { IRootState } from ".";

export interface ITheme {
  primary: string;
  secondary: string;
  accent: string;
  dark: boolean;
}

export interface ISettingsModule {
  theme: ITheme;
  user: {
    name: string;
  };
  language: Language;
}

export type Language = "en" | "de";

const mutations: MutationTree<ISettingsModule> = {
  setDark(state, isDark: boolean) {
    state.theme.dark = isDark;
  },
  setLanguage(state, locale: Language) {
    state.language = locale;
  },
  setTheme(state, theme) {
    state.theme = theme;
  }
};

const getters: GetterTree<ISettingsModule, IRootState> = {
  theme(state) {
    return {
      dark: state.theme.dark,
      ...state.theme
    };
  },
  user(state) {
    return state.user;
  },
  language(state) {
    return state.language;
  }
};

export const settingsModule: Module<ISettingsModule, IRootState> = {
  namespaced: true,
  state: {
    theme: {
      dark: true,
      primary: "#456CB2",
      secondary: "#424242",
      accent: "#82B1FF"
    },
    user: {
      // name: "Max Mustermann"
      name: ""
    },
    language: "de"
  },
  mutations,
  getters
};
