import store from "../../store";
import { RouterMiddleware } from "..";

/**
 * The user is not allowed to visit this page if they are authenticated
 * @param {*} param0
 */
export const isNotAuthenticated: RouterMiddleware = function({ next }) {
  const isLoggedIn = store.getters["auth/isLoggedIn"];
  if (!isLoggedIn) {
    return next(); // continue to the route
  }
  return next({
    path: "/" // redirect to /login if user is not logged in
  });
};
