import { Vue, Component, Watch } from "vue-property-decorator";

@Component
export class LanguageUpdaterMixin extends Vue {
  @Watch("currentLanguage", { immediate: true })
  updateI18nLocale() {
    this.$i18n.locale = this.currentLanguage;
    this.$vuetify.lang.current = this.currentLanguage;
  }

  get currentLanguage() {
    return this.$store.getters["settings/language"];
  }
}
